*{
  text-decoration: none;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;

}

.App-link {
  color: #61dafb;
}

.nav-space {
  height: 65px;
}

.spinner {
  position: absolute;
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

/* ------------------ NAV ------------------ */

.nav-bar {
  z-index: 1;
  background-color: #000;
  height: 60px ;
  width: 100%;
  position: fixed;
  display: flex;
  align-content: center;

}

.nav-item {
  display: flex;
  align-items: center;
}

.nav-item a:hover  {
  background-color: #282c34;
}

.nav-item-text {
  display: flex;
  align-items: center;
  text-decoration: none;
  color : #ffff;
  padding: 15px;
}

.nav-item-wallet{
  color : #ffff;
  padding: 15px;
  display: flex;
  justify-content: flex-end;
}

.nav-button {
  display: flex;
  height: 40px;
}

/* ------------------ HOMEPAGE ------------------ */

.div-home-0 {
  height: (100vh - 65px);
  display: flex;
  
}

.div-home-1 {
  display: flex;
  justify-content: center;
  margin-top: 20px;  
}

.div-home-2 {
  background-color: #13151a;
  width: 700px;
  height: 500px;
  overflow: hidden;
  overflow-y: scroll;
  border: solid;
  border-radius: 10px;
  border-width: 5px;
  border-color: black;
  box-shadow: 5px 5px 10px 5px #13151a;
}

.div-home-2 h2 {
  z-index: 0;
  display: flex;
  justify-content: center;
  position: absolute;
  background-color: black;
  width: 695px;
  height: 50px;
}

.h2-place {
  height: 50px;
}

.div-match-0 {
  background-color: #13151a;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: solid;
  border-color: black;
  padding: 50px;

}

.div-game-info{
  display: flex;
  font-size: 13px;
}


/* ------------------ SHOP ------------------ */

.div-shop {
  display: flex;
  justify-content: center;
  margin-top: 20px;  

}

.div-shop-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 250px;
  height: 250px;
  padding: 15px;

  background-color: #13151a;
  border-radius: 10px;
  box-shadow: 2px 2px 10px 2px #13151a;

}

.div-shop-product {
  display: flex;
  flex-direction: column;
  align-content: center;

}

.div-shop-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 5px;

}

.sailing-coin-img {
  display: flex;
  align-self: center;
  width: 100px;
  height: 100px;
}

#checkout-button {
  border-radius: 10px;
  background-color: white;
  border: none;
  
}

#checkout-button:hover {
  background-color: rgb(139, 139, 139);

}

/* ------------------ PROFILE ------------------ */

.profile-container {
  display: flex;
  justify-content: center;

}

.div-profile-0 {
  width: 96%;
  height: 600px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  background-color: #13151a;
  border-radius: 10px;
  box-shadow: 2px 2px 10px 2px #13151a;
  padding: 20px;

  

}

.div-profile-1 {
  width: 600px;
  height: 500px;

}

.div-profile-2 {
  width: 750px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.div-profile-info {
  background-color: white;
  padding: 5px;
  color: #000;
  border-radius: 10px;
}

.profile-img {
  border-radius: 300px ;
  width: 500px;
  height: 500px;
}

/* ------------------ TEAMS ------------------ */

.teams{
  padding-top: 50px;
  display: grid;
  justify-content: center;
  grid-template-columns: 300px 300px 300px;
  grid-template-rows: 300px 300px 300px;

}

.div-team-1  {
  background-color: #13151a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 10px 0px 10px;
  width: 250px;
  height: 250px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px 2px #13151a;
}

.add-team {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  border-radius: 300px;
  border: solid;
  border-color: white;
  background-color: white;
}

.add-team:hover {
  border-color: rgb(139, 139, 139);
  background-color: rgb(139, 139, 139);
}

.add-team-img {
  width: 60px;
  height: 60px;

}

.div-team-description {
  padding-top: 5px;

}

.teamcard {
  background-color: #13151a;
  display: flex;
  flex-direction: column;
  padding: 0px 10px 0px 10px;
  width: 250px;
  height: 250px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px 2px #13151a;
}

.teamcard-info {
  display: flex;
  justify-content: space-between;

}

.teamcard-list{
  list-style: none ;
  font-size: medium;
}

.teamcard-list li {
  margin-top: 5px;
}
